#nav-model {
    h2 {
        background-color: #247294;
        text-align: center;
        color: #fff;
        font-size: 14px;
    }
    input {
        width: 100%;
        border: 1px solid #ccc;
        margin-bottom: 15px;
        padding-left: 10px;
        border-radius: 3px;
    }
}

